import { createWebHistory, createRouter } from "vue-router";
import hello from './components/BlogList.vue';
import intro from './components/IntroVue.vue';
import detail from './components/DetailVue.vue';
import notpage from './components/404Vue.vue';
import AuthorVue from './components/AuthorVue.vue';
import CommentVue from './components/CommentVue.vue';
const routes = [
  {
    path: "/list",
    component: hello,
  },
  {
    path: "/intro",
    component: intro,
  },
  {
    path: "/detail/:id",
    component: detail,
    children:[
      {
        path : "author",
        component : AuthorVue
      },
      {
        path : "comment",
        component : CommentVue
      },
    ]
  },
  {
    path: "/detail/:anything(.*)",
    component: notpage,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 